-height: 44px;\n  display: block;\n}\n.sidebar #side li a::after {\n  content: \"\";\n  position: absolute;\n  bottom: 0;\n  left: 50%;\n  width: 0;\n  height: 1.5px;\n  transform: translateX(-50%);\n  background-color: #000000;\n  transition: width 0.3s ease;\n}\n.sidebar #side li:hover a::after {\n  width: 5rem;\n}\n#check {\n  display: none;\n}\nlabel #btn,\nlabel #cancel {\n  position: fixed;\n  cursor: pointer;\n  z-index: 110;\n}\nlabel #btn {\n  left: 25px;\n  top: 0.7rem;\n  font-size: 30px;\n  color: black;\n  border-radius: 3px;\n  padding: 5px 10px;\n  transition: all 0.5s;\n  position: fixed;\n  z-index: 100;\n  min-width: 44px;\n  min-height: 44px;\n}\n\nlabel #cancel {\n  left: -195px;\n  top: 17px;\n  font-size: 30px;\n  color: #000000;\n  padding: 4px 9px;\n  transition: all 0.5s ease;\n}\n#check:checked ~ .sidebar {\n  left: 0;\n}\n#check:checked ~ label #btn {\n  left: 250px;\n  opacity: 0;\n  pointer-events: none;\n}\n#check:checked ~ label #cancel {\n  left: 195px;\n}\n#btn {\n  display: none;\n}\n#judul {\n  font-weight: bolder;\n}\n/*responsive sidebar and tulisan front end dev*/\n@media (max-width: 575px) {\n  #check:checked ~ .sidebar {\n    left: 0;\n  }\n  #check:checked ~ label #btn {\n    left: 250px;\n    opacity: 0;\n    pointer-events: none;\n  }\n  #check:checked ~ label #cancel {\n    left: 195px;\n  }\n  label {\n    display: block;\n  }\n  #btn {\n    display: block;\n  }\n  #navbar li {\n    display: none;\n  }\n  #nav-btn {\n    display: none;\n  }\n\n  nav #judul {\n    position: absolute;\n    right: 10%;\n    font-size: 18px;\n  }\n  #cancel {\n    display: block;\n  }\n  .contact-header,\n  .service,\n  .judul {\n    font-size: 13px;\n  }\n  .sidebar #side li a {\n    height: 44px;\n  }\n}\n\n/* header and navbar*/\n\nnav ul li {\n  list-style: none;\n  margin: 5px 20px 0 20px;\n  position: relative;\n}\n#navbar {\n  display: flex;\n}\nheader {\n  width: 100%;\n  position: fixed;\n  background-color: white;\n  z-index: 99;\n}\nnav {\n  top: 0;\n  right: 0;\n  align-items: center;\n  justify-content: space-between;\n  transition: all 0.5s ease;\n  display: flex;\n  margin: auto;\n  width: 90%;\n  height: 55px;\n  line-height: normal;\n  padding-left: 18px;\n}\n\nnav ul li::after {\n  content: \"\";\n  position: absolute;\n  bottom: 0;\n  left: 0;\n  height: 1.5px;\n  background: black;\n  transition: all 0.3s;\n  width: 0;\n}\n\nnav ul li:hover::after {\n  width: 100%;\n}\n\nnav ul li a {\n  color: var(--header);\n  min-width: 44px;\n  min-height: 44px;\n  display: block;\n  line-height: 44px;\n}\n\nspan {\n  color: #fa0;\n}\n\n/* landing page */\n.moto {\n  padding-top: 20%;\n  color: white;\n  font-size: 20px;\n}\n.moto-container {\n  width: 80%;\n  margin: auto;\n}\n.hero {\n  width: 100%;\n  height: 75vh;\n  padding-top: 60px;\n  position: relative;\n}\n.hero picture img {\n  position: absolute;\n  top: 0;\n  left: 0;\n  width: 100%;\n  height: 100%;\n  object-fit: cover;\n  z-index: -1;\n}\n\n@media screen and (min-width: 1200px) {\n  .hero {\n    min-width: 1000px;\n  }\n}\n#moto {\n  background-color: rgba(255, 170, 0, 0.8);\n  color: white;\n  padding: 0 30px 0 3px;\n}\n@media screen and (max-width: 1025px) {\n  .moto {\n    font-size: 15px;\n    padding-top: 5rem;\n  }\n  .hero {\n    height: 40vh;\n  }\n}\n@media screen and (max-width: 500px) {\n  .moto {\n    font-size: 12px;\n  }\n}\n\n/* list */\n\n#list {\n  color: #fa0;\n  font-size: 25px;\n  padding-top: 5rem;\n  margin-left: 6rem;\n}\n.list {\n  width: 85%;\n  margin: auto;\n}\n.list-cards {\n  display: grid;\n  gap: 16px;\n  grid-template-columns: repeat(auto-fill, minmax(150px, 240px));\n  place-content: center;\n}\n.card {\nwidth: 240px;\n  padding: 20px;\n  box-shadow: 0 0 3px rgba(0, 0, 0, 0.162);\n  height: 270px;\n}\n\n.card-head {\n  display: flex;\n  justify-content: space-between;\n  margin-bottom: 8px;\n}\n.card-title a {\n  font-size: 18px;\n  color: #34364a;\n  font-weight: 700;\n  display: block;\n}\n.card-head p {\n  font-weight: 600;\n  color: rgb(253, 198, 88);\n}\n.card img {\n  width: 200px;\n  height: 150px;\n  position: relative;\n  z-index: 1;\n  margin-bottom: 6px;\n}\n\n@media (max-width: 500px) {\n  .card {\n    margin-top: 10px;\n  }\n  #list {\n    margin-left: auto;\n  }\n}\n/* skeleton ui */\n.skeleton {\n  min-width: 200px;\n  padding: 20px;\n  box-shadow: 0 0 3px rgba(0, 0, 0, 0.162);\n  min-height: 200px;\n}\n\n.skeleton-head {\n  display: flex;\n  justify-content: space-between;\n  margin-bottom: 8px;\n}\n\n.skeleton-line {\n  width: 35%;\n  height: 20px;\n  background: #e0e0e0;\n  border-radius: 4px;\n  animation: shimmer 1.5s infinite linear;\n  margin-bottom: 8px;\n}\n\n.skeleton-line.short {\n  width: 20%;\n}\n\n.skeleton-image {\n  width: 200px;\n  height: 150px;\n  background: #e0e0e0;\n  border-radius: 4px;\n  animation: shimmer 1.5s infinite linear;\n  margin-bottom: 6px;\n}\n\n@media (max-width: 500px) {\n  .skeleton {\n    margin-top: 10px;\n  }\n  #list {\n    margin-left: auto;\n  }\n}\n\n/* Shimmer effect */\n@keyframes shimmer {\n  0% {\n    background: linear-gradient(\n      90deg,\n      #e0e0e0 25%,\n      #f0f0f0 50%,\n      #e0e0e0 75%\n    );\n    background-size: 200% 100%;\n    background-position: -100% 0;\n  }\n  100% {\n    background-position: 100% 0;\n  }\n}\n\n\n/* footer */\n:root {\n  --header: #34364a;\n  --child: #b0b0b0;\n}\nfooter {\n  display: flex;\n  justify-content: space-around;\n  max-width: 90%;\n  margin: auto;\n  margin-bottom: 16px;\n  margin-top: 20px;\n}\n.contact {\n  padding-right: 0;\n  width: 32%;\n}\n.about {\n  width: 30%;\n}\n.about-desc,\n.contact-list li,\nfooter p {\n  color: var(--child);\n}\n.about-desc,\n.contact-list li {\n  padding: 10px 0 10px 0;\n}\n\n@media screen and (max-width: 1024px) {\n  .about-desc,\n  .contact-list li {\n    font-size: 13px;\n  }\n}\n@media screen and (max-width: 573px) {\n  footer {\n    flex-direction: column;\n    align-items: center;\n    justify-content: center;\n  }\n  .about,\n  /* .services, */\n  .contact {\n    width: 100%;\n    text-align: center;\n  }\n}\n\n/* submission */\n#skip-button {\n  position: absolute;\n  top: -50px;\n  font-size: 22px;\n  left: 0;\n  background-color: red;\n  color: white;\n  padding: 5px;\n  z-index: 99999;\n}\n#skip-button:focus {\n  top: 0;\n}\n\n/* detail page */\n.detail {\n  min-width: 65%;\n  padding: 70px 20px 20px 20px;\n  margin: auto;\n  display: grid;\n  grid-template-columns: 1fr; \n  grid-template-rows: auto auto auto; \n  gap: 20px;\n}\n\n.detail-main h1 {\n  font-size: 24px;\n  margin-bottom: 7px;\n  color: #fa0;\n}\n\n.detail span {\n  margin-left: 5px;\n}\n\n.detail img {\n  width: 100%;\n}\n\n.restaurant-information h3 {\n  margin-bottom: 2px;\n}\n\n.restaurant-information p {\n  margin-bottom: 5px;\n}\n\n.restaurant-information-menu {\n  margin-top: 16px;\n}\n\n.restaurant-information-menu-header {\n  color: #fa0;\n  font-size: 20px;\n}\n\n.restaurant-information-menu h3 {\n  margin: 2px 0;\n}\n\n.restaurant-description p{\n  font-size: 14px;\n}\n.restaurant-description {\n  height: fit-content;\n}\n/* comment section */\n/* styles.css */\n:root {\n  --text-color: #333333;\n  --accent-color: #ffd966; /* Kuning lebih pekat */\n  --border-color: #f0e1c2;\n}\n\n\n.comment-section {\n  padding: 16px;\n}\n\n.section-title {\n  font-size: 24px;\n  margin-bottom: 16px;\n  color: var(--accent-color);\n}\n\n.comments-container {\n  display: flex;\n  flex-direction: column;\n  gap: 8px;\n}\n\n.comment-card {\n  background: white;\n  padding: 16px;\n  border: 1px solid var(--border-color);\n  border-radius: 8px;\n}\n\n.comment-card .name {\n  font-weight: bold;\n  color: var(--text-color);\n}\n\n.comment-card .date {\n  font-size: 14px;\n  color: #666666;\n  margin-bottom: 8px;\n}\n\n.comment-card .review {\n  font-size: 16px;\n  line-height: 1.4;\n}\n\n@media screen and (min-width: 610px) {\n  .detail {\n    grid-template-columns: 1fr 1fr; \n    grid-template-rows: 1fr .3fr; \n    width: 70%;\n    padding-top: 150px;\n  }\n\n  .detail-main {\n    grid-column: 1 / 2;\n    grid-row: 1 / 2;    \n  }\n\n  .restaurant-information {\n    grid-column: 2 / 3; \n    grid-row: 1 / 2;    \n  }\n\n  .restaurant-description {\n    grid-column: 1 / 3; \n    grid-row: 2 / 3;    \n  }\n  .comment-section{\n    grid-column: 1 / 3; \n    grid-row: 3 / 3; \n  }\n\n  .detail img {\n    max-width: 400px; \n  }\n\n  .detail-main h1 {\n    font-size: 27px;\n  }\n\n  .restaurant-information-menu-header {\n    font-size: 22px;\n  }\n}\n\n\n/* like button */\n\n.like {\n  width: 55px;\n  height: 55px;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n\n  background-color: #ffd966;\n  position: fixed;\n  bottom: 16px;\n  right: 16px;\n  border-radius: 50%;\n  border: 0;\n  \n  font-size: 18px;\n  color: white;\n  cursor: pointer;\n}"],"sourceRoot":""}]);
